import "./styles.css";
import React, { useState } from "react";
import ReactDOM from "react-dom";

export default function App() {
  //ENTRADA, RODANDO E FIM
  const [estado, setEstado] = useState("ENTRADA");

  //palpite
  const [palpite, setPalpite] = useState(150);
  const [numPalpites, setNumPalpites] = useState(1);

  //limitando numero
  const [min, setMin] = useState(0);
  const [max, setMax] = useState(300);

  const iniciarJogo = () => {
    setEstado("RODANDO");
    setPalpite(150);
    setMin(0);
    setMax(300);
    setNumPalpites(1);
  };

  if (estado === "ENTRADA") {
    return (
      <div className="container-btnInicio">
        <button className="btnInicio" onClick={iniciarJogo}>
          Vamos começar!
        </button>
      </div>
    );
  }

  const menor = () => {
    setNumPalpites(numPalpites + 1);
    setMax(palpite);
    const proxPalpite = parseInt((palpite - min) / 2) + min;
    setPalpite(proxPalpite);
  };

  const maior = () => {
    setNumPalpites(numPalpites + 1);
    setMin(palpite);
    const proxPalpite = parseInt((max - palpite) / 2) + palpite;
    setPalpite(proxPalpite);
  };

  const acertou = () => {
    setEstado("FIM");
  };
  const jogarNovo = () => {
    setEstado("ENTRADA");
  };
  if (estado === "FIM") {
    return (
      <div className="result">
        <p class="text">
          O numero escolhido foi {palpite} e acertamos em {numPalpites}{" "}
          palpites!
        </p>
        <button className="button" onClick={jogarNovo}>
          jogar de novo
        </button>
      </div>
    );
  }

  return (
    <div className="App">
      <p class="text">o seu número é {palpite}?</p>
      <button className="btnMenor" onClick={menor}>
        Menor
      </button>
      <button className="btnAcertou" onClick={acertou}>
        Acertou
      </button>
      <button className="btnMaior" onClick={maior}>
        Maior
      </button>
    </div>
  );
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
